.post{
    background-color: white;
    padding: 15px;
    margin-bottom: 10px;
    border-radius: 10px;
}
.post__header{
    display: flex;
    padding-bottom: 10px;
    align-items: center;
}
.post__info{
    margin-left: 10px;
}
.post__info>p{
    font-size: 12px;
    color: gray;
}
.post__info>h2{
    font-size: 15px;
}
.post__body{
    overflow-wrap: normal;
    margin-bottom: 15px;
}
.post__buttons{
    display: flex;
    justify-content: space-evenly;
}